// меньше чем 1440px
@media (max-width: 1440px){

}

@media (max-width: 1240px){
  .logo{
      img{
          width: 100px;
      }
  }
  .header__title{
      font-size: 130px;
      line-height: 108px;
      top: 20px;
      left: 60px;
  }
  .menu{
      padding-top: 10px;
  }
  .header__date{
      font-size: 60px;
      span{
          font-size: 13px;
          margin-bottom: 5px;
      }
  }
  .header__arrows{
      left: 130px;
  }
  .surf-box__inner{
      min-height: 650px;
  }
}
@media (max-width: 1365px){
  .header__aside{
      position: absolute;
      border-right: none;
      height: 108px;
      padding-right: 20px;
      background-color: rgba(#fff, .1);
      text-align: left;
      display: flex;
  }
  .menu{
      padding-top: 0;
  }
  .menu__list{
      text-align: center;
     li{
         display: inline-block;
     }
     a{
         padding: 15px;
     }
  }
  .header__search{
      padding: 33px 15px 15px;
      width: 30px;
  }
  .header__date{
      padding: 15px;
  }
  .header__location{
      margin-top: 42px;
  }
  .slider-item__info-title{
      font-size: 65px;
  }
  .header__arrows{
      left: 20px;
      bottom: 20px;
  }
  .shop__slider-content{
      padding-left: 40px;
  }
  .shop__slider-price{
      margin-bottom: 30px;
  }
}
@media (max-width: 1140px){
  .header__map{
      svg{
          width: 500px;
          height: 250px;
      }
  }
  .slider-arrows {
      top: 40px;
      right: 20px;
  }
  .slider-arrows__left {
      right: 60px;
  }
}
@media (max-width: 1250px){
  .slider-map .slick-list{
      overflow: hidden;
  }
  .slider-dots__circle{
      box-shadow: none;
  }
}
@media (max-width: 1250px){
  .slider-map{
      max-width: 1000px;
      min-height: 515px;
  }
  .slider-map .slick-list{
      min-height: 515px;
  }

  .slider-dots--1{
  top: 266px;
  left: 0;
  .slider-dots__content{
      transform: translateX(0);
      &::before{
          left: 0;
      }
  }
}
  .slider-dots--2{
      top: 232px;
      left: 0;
      .slider-dots__circle{
          position: relative;
          &::before{
              content: "";
              background-image: url(../img/ms-img/min-marker.png);
              width: 14px;
              height: 17px;
              position: absolute;
              background-size: cover;
              right: -25px;
              top: -25px;

          }
      }
  }
  .slider-dots--3{
      top: 250px;
      left: 25px;
  }
  .slider-dots--4{
      top: 400px;
      left: -27px;
  }
  .slider-dots--5{
      top: 210px;
      left: -65px;
  }
  .slider-dots--6{
      top: 440px;
      left: -120px;
  }
  .slider-dots--7{
      top: 285px;
      left: -95px;
  }
  .slider-dots--8{
      top: 470px;
      left: 35px;
      .slider-dots__content{
          transform: translateX(-262px);
          &::before{
              left: auto;
              right: 0;
          }
      }
  }
  .shop__slider-inner{
      display: block;
  }
  .shop__slider-info{
      padding-top: 55px;
      margin-bottom: 50px;
  }
  .shop__slider{
      padding-left: 20px;
  }
  .surfboard__position--3 .surfboard-box__content{
      left: 60px;
  }
  .surfboard__position--3 .surfboard-box__content::before {
  content: "";
  width: 20px;
  right: auto;
  left: -32px;
  }
}
@media (max-width: 1150px){
  .holder-slider__descr{
      padding: 160px 0 0 20px;
  }
  .surf, .holder{
      padding: 70px 0 50px;
  }
}
@media (max-width: 1102px){
  .slider-map{
      background-image: none;
      max-width: 100%;
      min-height: 280px;
  }
  .slider-dots__circle{
      display: none;
  }
  .slider-dots__content{
      display: block;
      &::before{
          display: none;
      }
  }
  .slider-dots{
      position: static;
      }
      .slider-map{
          .slick-list{
              overflow: hidden;
              min-height: 280px;
          }
          .slick-track{
              min-height: 280px;
      }
  }
  .slider-dots__content,
  .slider-dots--8 .slider-dots__content{
      transform: translateX(0);
      width: 285px;
      padding: 28px 31px 0 32px;
  }
  .slider-map .slick-current .slider-dots__content{
      background-color: rgba(#ccc, 0.7);
  }
}

@media (max-width: 1040px){
  .header__arrows{
      display: none;
  }
  .menu{
      background-color: #000;
      z-index: 22;
      position: absolute;
      transform: translateX(-100%);
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 16px;
      transition: all .5s;
      &.active{
          transform: translateX(0);
      }
  }
  .menu-btn{
      display: flex;
  }
  .header__search{
      padding: 40px 15px 15px;
  }
  .slider-item__info {
      position: absolute;
      right: 20px;
      bottom: 17px;
      text-align: right;
  }
  .slider-item__info-text {
      font-weight: 800;
      font-size: 30px;
      line-height: 20px;
      margin-bottom: 15px;
  }
  .headline-left {
      font-size: 16px;
      line-height: 18px;
      padding-left: 65px;
  }
  .headline-right{
      font-size: 16px;
      line-height: 18px;
      padding-right: 65px;
  }
  .slider-item__info-subtext {
      margin-bottom: 5px;
  }
  .headline-left::before,
  .headline-right::before
  {
      width: 50px;
  }
  .slider-item__info-title {
      font-weight: 800;
      font-size: 55px;
      line-height: 55px;
      margin-bottom: 20px;
  }
  .slider-dotshead {
      right: 0px;
      left: 0;
      bottom: 5px;
      margin: 0 auto;
  }
  .dots-box {
  width: 125px;
  }
  .dots-box__number{
      font-size: 18px;
  }
  .dots-box__name{
      line-height: 18px;
  }
  .surf__subtitle{
      padding-right: 20px;
  }
  .holder-slider__info{
      display: block;
  }
  .holder-slider__info-subtitle{
      position: static;
      transform: rotate(0);
      height: auto;
      width: auto;
      text-align: left;
  }
  .holder-slider__info-item{
      min-height: auto;
      margin-bottom: 40px;
      padding-left: 20px;
  }
  .holder-slider__info-title{
      padding-left: 0;
  }
  .holder-slider__descr{
      background-position: right -139px;
  }
  .holder {
      .title{
          span{
              padding-bottom: 0;
          }
      }
  }
  .holder-slider__info-title .holder-slider__pricing-span{
      text-align: left;
      bottom: -22px;
      left: 0;
  }
  .quantity-button.quantity-up{
      right: auto;
      left: 25px;
  }
  .quantity-button.quantity-down {
      right: auto;
  }
  .shop__extras-title{
      padding-left: 240px;
  }
  .shop__extras-inner{
      padding-left: 240px;
  }
}
@media (max-width: 960px){
  .slider-dotshead{
      display: none;
  }
  .shop__extras-title{
      padding-left: 140px;
  }
  .shop__extras-inner{
      padding-left: 140px;
  }
}
@media (max-width: 767px){
  .surfboard-box__content{
      padding-left: 0;
      top: 35px;
      left: 0;
      width: 270px;
      &::before{
          display: none;
      }
  }
  .surfboard__position--1{
      .surfboard-box__content{
          left: -177px;
      }
  }
  .shop__extras{
      padding-left: 75px;
  }
  .shop__extras-title{
      padding-left: 40px;
  }
  .shop__extras-inner{
      padding-left: 40px;
  }
}
@media (max-width: 720px){
  .title{
      span{
          font-size: 87px;
          padding-bottom: 80px;
          padding-left: 45px;
      }
  }
  .surf-slider .slider-arrows{
      top: 20px;
  }
  .surf-slider{
      padding-top: 65px;
  }
  .holder-slider__head{
      min-height: 720px;
  }
  .holder-slider__subtitle{
      padding: 100px 20px 0 0;
  }
}
@media (max-width: 680px){
  .shop__slider-content{
      display: block;
  }
  .shop__extras{
      padding-left: 0;
      padding-top: 35px;
  }
  .shop__extras-title{
      padding-left: 0;
  }
  .shop__extras-inner{
      padding-left: 0;
  }
  .surfboard__position--1{
      left: 155px;
  }
  .footer{
      padding: 100px 0 34px;
  }
}
@media (max-width: 640px){
  .holder-slider__descr{
      background-image: none !important;
  }
  .btn{
      font-size: 30px;
  }
  .holder-slider__descr .descr__head .holder__star-list{
      top: auto;
      left: 0;
  }
}
@media (max-width: 520px){
  .headline-left{
      letter-spacing: 2.33333px;
  }
  .subtitle__head{
      font-size: 32px;
      padding-right: 0;
  }
  .descr__head{
      font-size: 32px;
  }
}
@media (max-width: 460px){
  .header__aside{
      display: block;
      text-align: center;
      padding-right: 0;
      height: 100vh;
  }
  .header__search{
      padding: 15px 40px 10px;
  }
  .header__date{
      padding: 0;
      font-size: 50px;
      line-height: 45px;
      span{
          font-size: 12px;
      }
  }
  .header__location{
      margin-top: 0px;
  }
  .header__title{
      display: none;
  }
  .holder{
      background-size: 120px;
  }
  .header__map svg {
      width: 400px;
      height: 400px;
  }
}
@media (max-width: 425px){
  .slider-map .slider-dots__content,
  .slider-map .slick-current .slider-dots__content{
      width: 250px;
      padding: 28px 15px 0;
  }
  .shop__extras-box{
      margin-right: 16px;
  }
}
@media (max-width: 374px){
  .slider-map .slider-dots__content,
  .slider-map .slick-current .slider-dots__content{
      display: block;
      width: 240px;
      padding: 28px 10px 0;
  }
}
